"use client"

import type React from "react"

import { useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { Label } from "@/components/ui/label"
import { useToast } from "@/hooks/use-toast"
import { useAuth } from "@/components/auth-provider"
import { submitWhyForm } from "@/lib/api"

export default function LandingPage() {
  const [name, setName] = useState("")
  const [why, setWhy] = useState("")
  const [isLoading, setIsLoading] = useState(false)
  const router = useRouter()
  const { toast } = useToast()
  const { login } = useAuth()

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()

    if (!name.trim() || !why.trim()) {
      toast({
        title: "Error",
        description: "Please fill in all fields",
        variant: "destructive",
      })
      return
    }

    setIsLoading(true)

    try {
      const response = await submitWhyForm(name, why)

      if (response.admin && response.token) {
        login(response.token)
        toast({
          title: "Welcome, Admin",
          description: "Redirecting to dashboard...",
        })
        router.push("/admin")
      } else {
        toast({
          title: "Welcome",
          description: "Redirecting to home page...",
        })
        router.push("/home")
      }
    } catch (error) {
      toast({
        title: "Error",
        description: error instanceof Error ? error.message : "Failed to submit form",
        variant: "destructive",
      })
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="min-h-screen bg-[#F5F5F0] flex flex-col">
      {/* Header */}
      <header className="py-12 text-center border-b border-[#E5E5E0]">
        <h1 className="font-serif text-6xl font-bold text-black tracking-tight">AM</h1>
      </header>

      {/* Main Content */}
      <main className="flex-1 flex items-center justify-center px-4 py-16">
        <div className="w-full max-w-md">
          <div className="text-center mb-10">
            <h2 className="font-serif text-3xl font-bold text-black mb-3">Welcome to the AM Journal</h2>
            <p className="font-sans text-gray-600">Tell us a bit about yourself to continue</p>
          </div>

          <form onSubmit={handleSubmit} className="space-y-6">
            <div className="space-y-2">
              <Label htmlFor="name" className="font-sans text-sm text-black">
                Your Name
              </Label>
              <Input
                id="name"
                type="text"
                value={name}
                onChange={(e) => setName(e.target.value)}
                placeholder="Enter your name"
                className="bg-white border-[#E5E5E0] focus:border-black focus:ring-black"
                disabled={isLoading}
              />
            </div>

            <div className="space-y-2">
              <Label htmlFor="why" className="font-sans text-sm text-black">
                Why are you here?
              </Label>
              <Textarea
                id="why"
                value={why}
                onChange={(e) => setWhy(e.target.value)}
                placeholder="Tell us what brings you to AM Journal..."
                rows={4}
                className="bg-white border-[#E5E5E0] focus:border-black focus:ring-black resize-none"
                disabled={isLoading}
              />
            </div>

            <div className="flex gap-3">
              <Button
                type="submit"
                className="flex-1 bg-black text-white hover:bg-black/90 rounded-md py-3 font-sans"
                disabled={isLoading}
              >
                {isLoading ? "Submitting..." : "Continue"}
              </Button>
              <Button
                type="button"
                onClick={() => router.push("/home")}
                className="flex-1 bg-white text-black border border-black hover:bg-gray-100 rounded-md py-3 font-sans"
                disabled={isLoading}
              >
                Skip
              </Button>
            </div>
          </form>
        </div>
      </main>

      {/* Footer */}
      <footer className="py-6 text-center border-t border-[#E5E5E0]">
        <p className="font-sans text-sm text-gray-600">Â© 2025 AM Journal. All rights reserved.</p>
      </footer>
    </div>
  )
}
